<template>
  <v-sheet width="300" class="mx-auto">
    <v-form ref="form">
      <v-select
        v-model="prefecture"
        :items="prefectures"
        label="都道府県"
        density="compact"
        required
        @change="changePrefecture"
      ></v-select>

      <v-select
        v-model="area"
        :items="areas"
        label="地域"
        density="compact"
        required
        @change="changeArea"
      ></v-select>

      <div class="d-flex flex-column">
        <v-btn color="success" class="mt-4" block @click="submit"> 登録 </v-btn>
      </div>
    </v-form>
  </v-sheet>
</template>

<script>
import { defineComponent, ref } from "@vue/composition-api";

export default defineComponent({
  name: "LocationSetting",
  setup() {
    const prefectures = ["佐賀", "宮崎", "鹿児島"];
    const prefecture = ref(prefectures[0]);
    const areas = ["北部", "南部"];
    const area = ref(areas[0]);

    const changePrefecture = (newVal) => {
      prefecture.value = newVal;
    };
    const changeArea = (newVal) => {
      area.value = newVal;
    };
    const submit = () => console.log(prefecture.value, area.value);

    return {
      prefectures,
      prefecture,
      areas,
      area,
      changePrefecture,
      changeArea,
      submit,
    };
  },
});
</script>
